body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
  background: linear-gradient(-45deg, #6e1c52, #6b0e4d, #14546d, #0a5f55);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
}

.App {
  text-align: center;
}

header {
  background-color: #0505059a;
  padding: 20px 0;
  animation: fadeIn 1s ease-in-out;
}

header h1 {
  margin: 0;
  color: #ffd700; /* Yellow */
}

nav {
  background-color: #05050569;
  padding: 10px 0;
  animation: slideIn 1s ease-in-out;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

nav li {
  display: inline;
  margin-right: 20px;
}

nav a {
  text-decoration: none;
  color: #ffd700; /* Yellow */
  font-weight: bold;
  font-size: 18px;
  transition: color 0.3s ease-in-out;
}

nav a:hover {
  color: #b3b300; /* Darker Yellow */
}

section {
  padding: 50px 0;
  animation: fadeIn 1s ease-in-out;
}

section h2 {
  color: #ffd700; /* Yellow */
}

section p {
  color: #ccc; /* Light Gray */
}

footer {
  color: #160a0a;
  text-align: center;
  padding: 10px 0;
}

.github-button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #5c5c5c;
  color: #ffd700; /* Yellow */
  text-decoration: none;
  border-radius: 5px;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.github-button:hover {
  background-color: #4d4d4d;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
  }
  to {
    transform: translateY(0);
  }
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Adjust the number of columns as needed */
  gap: 20px;
  margin-top: 20px;
}

.project-card {
  color: #300707;
  color: #fff;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease-in-out;
}

.project-card:hover {
  transform: scale(1.05);
}

.github-link {
  display: inline-block;
  margin-top: 10px;
  padding: 8px 15px;
  background-color: #5c5c5c;
  color: #ffd700; /* Yellow */
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.github-link:hover {
  background-color: #4d4d4d;
}

@keyframes gradientAnimation {
  0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

/* Align Profile and Work Experience to the right */
#profile, #experience {
  text-align: left;
  margin-left: 120px;
}

/* Align Education to the left */
/* #education {
  text-align: right;
  margin-right: 120px;
} */

.profile-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.profile-content {
  display: flex;
  align-items: left;
  flex-wrap: wrap;
}

.profile-image {
  margin-right: 20px; /* Adjust the margin as needed */
}

.profile-img {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  transition: transform 0.3s ease-in-out;
}

.profile-img:hover {
  transform: scale(1.1);
}

.profile-description {
  max-width: 1000px; /* Adjust the max-width as needed */
}

.education-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.education-card {
  color: #fff;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease-in-out;
}

.education-card:hover {
  transform: scale(1.05);
}

.work-experience-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.work-experience-card {
  color: #fff;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease-in-out;
}

.work-experience-card:hover {
  transform: scale(1.05);
}
